import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { ButtonType } from '@shared/components/button/button.component';
import { FeedbackFacade } from './feedback.facade';
import { MatDialog } from '@angular/material/dialog';
import { FeedbackSuccessDialogComponent } from './feedback-success-dialog/feedback-success-dialog.component';
import { SnackService } from '@core/services/snack.service';
import { Router } from '@angular/router';
import { AuthService } from '@core/data-access/services/auth.service';
import { MatRadioButton } from '@angular/material/radio';

@Component({
    selector: 'findqo-feedback',
    templateUrl: './feedback.component.html',
    styleUrl: './feedback.component.scss',
    standalone: false
})
export class FeedbackComponent implements OnInit {
	readonly buttonType = ButtonType;

	feedbackRating = [];
	feedbackForm = this.fb.group({
		ratingId: ['', [Validators.required]],
		role: ['', [Validators.required]],
		usageRate: ['', [Validators.required]],
		comment: ['', [Validators.required]],
		senderEmail: ['', [Validators.required, Validators.email]],
	});

	isLoading = true;

	authUser = !!this.auth.user();

	constructor(
		private fb: FormBuilder,
		private feedbackFacade: FeedbackFacade,
		private matDialog: MatDialog,
		private snackBar: SnackService,
		private router: Router,
		private auth: AuthService
	) {}

	onRatingSelected(event) {
		this.feedbackForm.patchValue({
			ratingId: event,
		});
	}

	onSubmit() {
		if (this.feedbackForm.invalid) {
			this.snackBar.open({
				data: { message: 'Please fill in all the required details!', type: 'error' },
				panelClass: 'snackbar-error',
			});
			return;
		}

		this.feedbackFacade.addFeedbackList(this.feedbackForm.value).subscribe(() => {
			this.matDialog.open(FeedbackSuccessDialogComponent, {
				maxWidth: '542px',
				width: '100%',
				disableClose: true,
			});
		});
	}

	onCancel() {
		this.router.navigate(['/']);
	}

	onUpdateRadioBtnChild(event: MatRadioButton, key, fromRadioButton: boolean): void {
		if (!fromRadioButton) {
			event.checked = !event.checked;
		}

		if (event.value || !fromRadioButton) {
			this.feedbackForm.get(key).setValue('');
		}

		if (event.value || fromRadioButton) {
			this.feedbackForm.get(key).setValue(event.value);
		}
	}

	ngOnInit(): void {
		this.feedbackFacade.loadFeedbackList().subscribe((res) => {
			if (res?.['data'] && res?.['data'].length) {
				this.feedbackRating = res?.['data'];
				this.isLoading = false;
			}
		});

		/**
		 * Removed sender email validator if user is already logined
		 */
		if (this.auth.user()) {
			this.feedbackForm.get('senderEmail').clearValidators();
			this.feedbackForm.get('senderEmail').updateValueAndValidity();
		}
	}
}
