import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Ad } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-feedback-success-dialog',
    templateUrl: './feedback-success-dialog.component.html',
    styleUrls: ['./feedback-success-dialog.component.scss'],
    standalone: false
})
export class FeedbackSuccessDialogComponent {
	constructor(
		@Inject(MAT_DIALOG_DATA) public readonly ad: Ad,
		private readonly dialogRef: MatDialogRef<FeedbackSuccessDialogComponent>
	) {}

	readonly buttonType = ButtonType;

	onDismiss(): void {
		this.dialogRef.close(false);
	}
}
