import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { FeedbackApi } from '@core/data-access/apis/feedback.api';
import { of } from 'rxjs';
import { isPlatformBrowser } from '@angular/common';

@Injectable()
export class FeedbackFacade {
	constructor(
		private readonly feedbackApi: FeedbackApi,
		@Inject(PLATFORM_ID) private platformId: string
	) {}

	loadFeedbackList() {
		if (!isPlatformBrowser(this.platformId)) {
			return of([]);
		}
		return this.feedbackApi.getFeedbackList();
	}

	addFeedbackList(params) {
		return this.feedbackApi.addFeedback(params);
	}
}
